import React from 'react'
import { Table } from 'antd'

const columns = [
  {
    title: '',
    align: 'center',
    dataIndex: 'name',
    key: 'name'
  },
  {
    title: '',
    align: 'center',
    dataIndex: 'age',
    key: 'age'
  },
  {
    title: '',
    align: 'center',
    dataIndex: 'address',
    key: 'address'
  },
  {
    title: '',
    dataIndex: 'oprate',
    align: 'center',
    key: 'oprate',
    render: (text, record, index) => {
      return (
        <div className="opetates">
          <span
            className="iconfont iconchakan"
            onClick={e => this.clickImgBtn(e, record, index)}
            title=""
          />
          <span
            className="iconfont icondownload"
            onClick={e => this.addGridLayer(e, record)}
            title=""
          />
          <span
            className="iconfont icondelete"
            onClick={e => this.addGridLayer(e, record)}
            title=""
          />
        </div>
      )
    }
  }
]

const dataSource = [
  {
    key: '1',
    name: '',
    age: 32,
    address: '1'
  },
  {
    key: '2',
    name: '',
    age: 42,
    address: '1'
  }
]

class TableList extends React.Component {
  constructor(props) {
    super(props)
    this.pageSize = 8
  }

  // 
  onPageChangePage = () => {}

  // 
  clickRow = () => {}

  render() {
    const total = dataSource.length

    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.warn(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows)
      },
      onSelect: (record, selected, selectedRows) => {
        console.warn(record, selected, selectedRows)
      },
      onSelectAll: (selected, selectedRows, changeRows) => {
        console.warn(selected, selectedRows, changeRows)
      }
    }
    return (
      <div className="htht-list htht-table-list">
        <Table
          rowKey={record => record.key}
          columns={columns}
          dataSource={dataSource}
          rowSelection={rowSelection}
          pagination={{
            total,
            pageSize: this.pageSize,
            current: 1,
            showQuickJumper: true,
            onChange: this.onPageChangePage.bind(this)
          }}
          onRow={record => {
            // 
            return {
              onClick: e => this.clickRow(e, record)
            }
          }}
        />
      </div>
    )
  }
}

export default TableList
